@import url(https://fonts.googleapis.com/css?family=Muli:400,400i,600,700,700i,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Megrim|Poiret+One|Roboto+Mono|Space+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Variables
/* ---------------------------------------------------------- */

:root {

    /* Colours */
    --color-primary: cyan;
    --color-base: cyan;
    --color-border: cyan;
    --color-bg: #f8f8f8;
    --color1: #011627;
    --color2: #b91372;
    --color3: #54086b;
    --color4: #00bec5;

    /* Fonts */
    --font-sans-serif: 'Megrim', cursive;
    --font-serif: 'Poiret One', cursive;
    --font-mono: 'Roboto Mono', monospace;
    --font-light: 100;
    --font-normal: 400;
    --font-bold: 700;
    --font-heavy: 800;

    /* Breakpoints */
    --xlarge: 1680px;
    --large: 1280px;
    --medium: 980px;
    --small: 740px;
    --xsmall: 480px;

    /* Sizes */
    --height: 4rem;
    --margin: 2rem;
    --radius: 0.5rem;

}

/* Reset
/* ---------------------------------------------------------- */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    font-size: 100%;
    vertical-align: baseline;
}
body {
    line-height: 1;
}
ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    border-spacing: 0;
    border-collapse: collapse;
}
img {
    max-width: 100%;
}
html {
    box-sizing: border-box;
    font-family: sans-serif;

    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
a {
    background-color: transparent;
}
a:active,
a:hover {
    outline: 0;
    text-decoration: none;
}
b,
strong {
    font-weight: bold;
}
i,
em,
dfn {
    font-style: italic;
}
h1 {
    margin: 0.67em 0;
    font-size: 2em;
}
small {
    font-size: 80%;
}
sub,
sup {
    position: relative;
    font-size: 75%;
    line-height: 0;
    vertical-align: baseline;
}
sup {
    top: -0.5em;
}
sub {
    bottom: -0.25em;
}
img {
    border: 0;
}
svg:not(:root) {
    overflow: hidden;
}
mark {
    background-color: #fdffb6;
}
code,
kbd,
pre,
samp {
    font-family: monospace, monospace;
    font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
    margin: 0;
    color: inherit;
    font: inherit;
}
button {
    overflow: visible;
    border: none;
}
button,
select {
    text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
    cursor: pointer;
    -webkit-appearance: button;
}
button[disabled],
html input[disabled] {
    cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
    padding: 0;
    border: 0;
}
input {
    line-height: normal;
}
input:focus {
    outline: none;
}
input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box;
    padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    height: auto;
}
input[type="search"] {
    box-sizing: content-box;

    -webkit-appearance: textfield;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}
legend {
    padding: 0;
    border: 0;
}
textarea {
    overflow: auto;
}
table {
    border-spacing: 0;
    border-collapse: collapse;
}
td,
th {
    padding: 0;
}

/* ==========================================================================
   Base styles: opinionated defaults
   ========================================================================== */

html {
    overflow-x: hidden;
    overflow-y: scroll;
    font-size: 62.5%;

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
    overflow-x: hidden;
    color: color(var(--color-base) l(+20%));
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 1.5rem;
    line-height: 1.6em;
    font-weight: 400;
    font-style: normal;
    letter-spacing: 0;
    text-rendering: optimizeLegibility;
    background: #fff;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -moz-font-feature-settings: "liga" on;
}

::-moz-selection {
    text-shadow: none;
    background: #cbeafb;
}

::selection {
    text-shadow: none;
    background: #cbeafb;
}

hr {
    position: relative;
    display: block;
    width: 100%;
    margin: 2.5em 0 3.5em;
    padding: 0;
    height: 1px;
    border: 0;
    border-top: 1px solid color(var(--color-border) l(+10%));
}

audio,
canvas,
iframe,
img,
svg,
video {
    vertical-align: middle;
}

p,
blockquote {
    margin: 0 0 1.5em 0;
}

blockquote {
    margin: 1em 0;
    padding: 0 1.6em 0 1.6em;
    border-left: cyan 0.1em solid;
    height: 150px;
}

blockquote p {
    margin: 0.8em 0;
    font-size: 1.2em;
    font-weight: 300;
}

blockquote small {
    display: inline-block;
    margin: 0.8em 0 0.8em 1.5em;
    font-size: 0.9em;
    opacity: 0.8;
}
/* Quotation marks */
blockquote small:before {
    content: "\2014 \00A0";
}

blockquote cite {
    font-weight: bold;
}
blockquote cite a {
    font-weight: normal;
}

a {
    color: color(var(--color-primary) l(-5%));
    text-decoration: none;
    -webkit-transition: 0.4s ease;
    transition: 0.4s ease;
}

a:hover {
    text-decoration: underline;
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 0;
    line-height: 1.4;
    font-weight: 700;
    text-rendering: optimizeLegibility;
}

h1 {
    margin: 0 0 0.5em 0;
    font-size: 5.7rem;
    font-weight: 800;
}
@media (max-width: 500px) {
    h1 {
        font-size: 2.7rem;
    }
}

h2 {
    margin: 1.5em 0 0.5em 0;
    font-size: 4rem;
}
@media (max-width: 500px) {
    h2 {
        font-size: 2rem;
    }
}

h3 {
    margin: 1.5em 0 1em 0;
    font-size: 3.2rem;
}
@media (max-width: 500px) {
    h3 {
        font-size: 1.8rem;
    }
}

h4 {
    margin: 1.5em 0 1em 0;
    font-size: 2.6rem;
}

h5 {
    margin: 1.5em 0 1em 0;
    font-size: 2.4rem;
}

h6 {
    margin: 1.5em 0 1em 0;
    font-size: 2.2rem;
}


/* Styling for Ghost-specific elements
/* ---------------------------------------------------------- */

.kg-card {
    margin: 7vw 0;
}

.kg-embed-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 100%;
}

.kg-image {
    max-width: 100%;
    width: 100%;
}

.kg-card figcaption {
    padding: 1.5rem;
    font-size: 1.3rem;
    line-height: 1.4em;
    font-weight: 600;
    color: var(--color-base);
    text-align: center;
    opacity: 0.4;
}

.kg-width-wide {
    position: relative;
    width: 75vw;
    min-width: 100%;
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    -webkit-transform: translateX(calc(50vw - 50%));
            transform: translateX(calc(50vw - 50%));
}

.kg-width-full {
    position: relative;
    width: 94vw;
    min-width: 100%;
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    -webkit-transform: translateX(calc(50vw - 50%));
            transform: translateX(calc(50vw - 50%));
}

.kg-width-full figcaption {
    padding-left: 0;
    padding-right: 0;
    text-align: left;
}

@media (max-width: 800px) {
    .kg-width-full {
        width: 100vw;
    }
    .kg-width-full figcaption {
        padding-left: 6vw;
    }
}

.kg-gallery-container {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 75vw;
    min-width: 100%;
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    -webkit-transform: translateX(calc(50vw - 50%));
            transform: translateX(calc(50vw - 50%));
}

.kg-gallery-row {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.kg-gallery-image img {
    display: block;
    margin: 0;
    width: 100%;
    height: 100%;
}

.kg-gallery-row:not(:first-of-type) {
    margin: 0.75em 0 0 0;
}

.kg-gallery-image:not(:first-of-type) {
    margin: 0 0 0 0.75em;
}

.kg-gallery-card + .kg-image-card.kg-width-wide,
.kg-gallery-card + .kg-gallery-card,
.kg-image-card.kg-width-wide + .kg-gallery-card,
.kg-image-card.kg-width-wide + .kg-image-card.kg-width-wide {
    margin-top: calc( -7vw + 0.75em );
}

/* Lists
/* ---------------------------------------------------------- */

ul,
ol,
dl {
    margin: 0 0 var(--margin) 0;
}

ol,
ul {
    padding-left: 1.3em;
    padding-right: 1.5em;
}

ol ol,
ul ul,
ul ol,
ol ul {
    margin: 0.5em 0 1em;
}

ul {
    list-style: disc;
}

ol {
    list-style: decimal;
}

ul,
ol {
    max-width: 100%;
}

li {
    margin: 0.5em 0;
    padding-left: 0.3em;
    line-height: 1.6em;
}


dt {
    display: block;
    font-weight: var(--font-bold);
    margin: 0 0 calc(var(--margin) * 0.5) 0;
}

dd {
    margin-left: var(--margin);
}

/* Grid
/* ---------------------------------------------------------- */

.row {
    display: flex;
    flex: 0 1 auto;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 -1rem;
}

.col,
.col-1,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-10,
.col-11,
.col-12 {
    padding: 1rem;
}

.col {
    flex-grow: 1;
    flex-basis: 0;
    max-width: 100%;
}

.col-1 {
    flex-basis: 8.3333333%;
    max-width: 8.3333333%;
}

.col-2 {
    flex-basis: 16.6666666%;
    max-width: 16.6666666%;
}

.col-3 {
    flex-basis: 25%;
    max-width: 25%;
}

.col-4 {
    flex-basis: 33.3333333%;
    max-width: 33.3333333%;
}

.col-5 {
    flex-basis: 41.6666666%;
    max-width: 41.6666666%;
}

.col-6 {
    flex-basis: 50%;
    max-width: 50%;
}

.col-7 {
    flex-basis: 58.3333333%;
    max-width: 58.3333333%;
}

.col-8 {
    flex-basis: 66.6666666%;
    max-width: 66.6666666%;
}

.col-9 {
    flex-basis: 75%;
    max-width: 75%;
}

.col-10 {
    flex-basis: 83.3333333%;
    max-width: 83.3333333%;
}

.col-11 {
    flex-basis: 91.6666666%;
    max-width: 91.6666666%;
}

.col-12 {
    flex-basis: 100%;
    max-width: 100%;
}

@media (max-width: 800px) {
    .col-1,
    .col-2,
    .col-3,
    .col-4,
    .col-5,
    .col-6,
    .col-7,
    .col-8,
    .col-9,
    .col-10,
    .col-11 {
        flex-basis: 50%;
        max-width: 50%;
    }
}

@media (max-width: 600px) {
    .col-1,
    .col-2,
    .col-3,
    .col-4,
    .col-5,
    .col-6,
    .col-7,
    .col-8,
    .col-9,
    .col-10,
    .col-11 {
        flex-basis: 100%;
        max-width: 100%;
    }
}

/* Tables
/* ---------------------------------------------------------- */

table {
    border-collapse: separate;
    margin: 0 0 var(--margin) 0;
    width: 100%;
    font-size: 0.8em;
}

th {
    color: var(--color-base);
    font-size: 0.9em;
    font-weight: var(--font-bold);
    padding: 0 0.7em 0.4em 0.7em;
    text-align: left;
}

td {
    padding: 0.4em 0.7em;
    border-left-width: 0;
    border-top-width: 0;
}

td:first-child {
    border-left-width: 1px;
}

tbody tr {
    border: solid 1px var(--color-border);
    border-left: 0;
    border-right: 0;
}

tbody tr:first-child td {
    border-top-width: 1px;
}

tbody tr:nth-child(2n + 1) {
    background-color: var(--color-bg);
}

tbody td {
    border: solid 1px var(--color-border);
    border-left-width: 0;
    border-top-width: 0;
}

tfoot {
    border-width: 0;
}

/* Forms
/* ---------------------------------------------------------- */

form {
    margin: 0 0 var(--margin) 0;
}

fieldset {
    margin: 0;
    padding: 0;
    border: 0;
}

label {
    color: var(--color-primary);
    display: block;
    font-size: 0.9em;
    font-weight: var(--font-bold);
    margin: 0 0 (var(--margin) * 0.5) 0;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="search"],
input[type="url"],
select,
textarea {
    background: var(--color-bg);
    border-radius: var(--radius);
    border: none;
    border: solid 1px var(--color-border);
    color: inherit;
    display: block;
    outline: 0;
    padding: 0 0.6em;
    text-decoration: none;
    width: 100%;
}

input[type="text"]:invalid,
input[type="password"]:invalid,
input[type="email"]:invalid,
input[type="tel"]:invalid,
input[type="search"]:invalid,
input[type="url"]:invalid,
select:invalid,
textarea:invalid {
    box-shadow: none;
}

input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="search"]:focus,
input[type="url"]:focus,
select:focus,
textarea:focus {
    border-color: var(--color-primary);
    box-shadow: 0 0 0 1px var(--color-primary);
}

select {
    height: var(--height);
    padding-right: var(--height);
    text-overflow: ellipsis;
}

select option {
    color: var(--color-primary);
    background: var(--color-bg);
}

select:focus::-ms-value {
        background-color: transparent;
    }

select::-ms-expand {
    display: none;
}


input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="search"],
input[type="url"],
select {
    height: var(--height);
}

textarea {
    padding: 0.3em 0.6em;
    resize: vertical;
}

input[type="checkbox"],
input[type="radio"] {
    display: block;
    margin-right: -2em;
    opacity: 0;
    width: 1em;
    z-index: -1;
}

input[type="checkbox"] + label,
input[type="radio"] + label {
    display: flex;
    align-items: center;
    color: var(--color-base);
    cursor: pointer;
    font-size: 1em;
    font-weight: var(--font-normal);
    padding-left: calc((var(--height) * 0.6) + 0.75em);
    padding-right: 2rem;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

input[type="checkbox"] + label:before,
input[type="radio"] + label:before {
    background: var(--color-bg);
    border-radius: var(--radius);
    border: solid 1px var(--color-border);
    content: '';
    display: inline-block;
    height: calc(var(--height) * 0.6);
    line-height: calc(var(--height) * 0.56);
    text-align: center;
    width: calc(var(--height) * 0.6);
    margin-right: 1rem;
}

input[type="checkbox"]:checked + label:before,
input[type="radio"]:checked + label:before {
    background: var(--color-primary);
    border-color: var(--color-primary);
    color: var(--color-bg);
    content: '✓';
}

input[type="checkbox"]:focus + label:before,
input[type="radio"]:focus + label:before {
    border-color: var(--color-primary);
    box-shadow: 0 0 0 1px var(--color-primary);
}


input[type="checkbox"] + label:before {
    border-radius: var(--radius);
}

input[type="radio"] + label:before {
    border-radius: 100%;
}

/* Buttons
/* ---------------------------------------------------------- */

input[type="submit"],
input[type="reset"],
input[type="button"],
button,
.button {
    display: inline-block;
    height: var(--height);
    padding: 0 2rem;
    border: 0;
    border-radius: var(--radius);
    cursor: pointer;
    font-family: var(--font-sans-serif);
    font-size: 1.4rem;
    font-weight: var(--font-normal);
    line-height: var(--height);
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    -webkit-transition: 0.4s ease;
    transition: 0.4s ease;
}

input[type="submit"].fit,
input[type="reset"].fit,
input[type="button"].fit,
button.fit,
.button.fit {
    width: 100%;
}

input[type="submit"].small,
input[type="reset"].small,
input[type="button"].small,
button.small,
.button.small {
    height: calc(var(--height) * 0.9);
    line-height: calc(var(--height) * 0.9);
    padding: 0 1.5rem;
    font-size: 1.2rem;
}

input[type="submit"].large,
input[type="reset"].large,
input[type="button"].large,
button.large,
.button.large {
    height: calc(var(--height) * 1.14);
    line-height: calc(var(--height) * 1.14);
    padding: 0 3rem;
    font-size: 1.6rem;
}


input[type="submit"].disabled,
input[type="submit"]:disabled,
input[type="reset"].disabled,
input[type="reset"]:disabled,
input[type="button"].disabled,
input[type="button"]:disabled,
button.disabled,
button:disabled,
.button.disabled,
.button:disabled {
    pointer-events: none;
    opacity: 0.4;
}



input[type="submit"],
input[type="reset"],
input[type="button"],
button,
.button {
    color: var(--color-primary) !important;
    background-color: transparent;
    box-shadow: inset 0 0 0 2px var(--color-primary);
}

input[type="submit"]:hover,
input[type="reset"]:hover,
input[type="button"]:hover,
button:hover,
.button:hover {
    text-decoration: none;
    color: color(var(--color-primary) l(-15%)) !important;
    box-shadow: inset 0 0 0 2px color(var(--color-primary) l(-10%));
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
}

input[type="submit"].primary,
input[type="reset"].primary,
input[type="button"].primary,
button.primary,
.button.primary {
    color: #fff !important;
    background-color: var(--color-primary);
    box-shadow: none;
}

input[type="submit"].primary:hover,
input[type="reset"].primary:hover,
input[type="button"].primary:hover,
button.primary:hover,
.button.primary:hover {
        background-color: color(var(--color-primary) l(-10%));
    }

/* Actions
/* ---------------------------------------------------------- */

.actions {
    display: flex;
    cursor: default;
    list-style: none;
    margin: calc(var(--margin) * -0.5);
    padding-left: 0;
}

.actions li {
    padding: 0 0 0 calc(var(--margin) * 0.5);
    vertical-align: middle;
}

.actions.special {
    justify-content: center;
    width: 100%;
    margin-left: 0;
}

.actions.special li:first-child {
    padding-left: 0;
}

.actions.stacked {
    flex-direction: column;
    margin-left: 0;
}

.actions.stacked li {
    padding: calc(var(--margin) * 0.65) 0 0 0;
}

.actions.stacked li:first-child {
    padding-top: 0;
}


.actions.fit {
    width: calc(100% + calc(var(--margin) * 0.5));
}

.actions.fit li {
    flex: 1 1 auto;
    width: 100%;
}

.actions.fit li > * {
    width: 100%;
}

.actions.fit.stacked {
    width: 100%;
}

@media (max-width: var(--xsmall)) {
    .actions:not(.fixed) {
        flex-direction: column;
