/* https://github.com/jonsuh/hamburgers */
.hamburger {
    display: flex;
    overflow: visible;
    margin: 0;
    padding: 2px 0;
    border: 0;
    color: inherit;
    font: inherit;
    text-transform: none;
    background-color: transparent;
    cursor: pointer;
    -webkit-transition: opacity 0.15s linear, -webkit-filter 0.15s linear;
    transition: opacity 0.15s linear, -webkit-filter 0.15s linear;
    transition: opacity 0.15s linear, filter 0.15s linear;
    transition: opacity 0.15s linear, filter 0.15s linear, -webkit-filter 0.15s linear;
}

.hamburger-box {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 13px;
}

.hamburger-inner {
    top: 50%;
    display: block;
    margin-top: -2px;
}

.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
    position: absolute;
    width: 20px;
    height: 1px;
    background-color: color(var(--color-border) l(-20%));
    border-radius: 4px;
    -webkit-transition: -webkit-transform 0.15s ease;
    transition: -webkit-transform 0.15s ease;
    transition: transform 0.15s ease;
    transition: transform 0.15s ease, -webkit-transform 0.15s ease;
}
.hamburger-inner::before,
.hamburger-inner::after {
    content: "";
    display: block;
}
.hamburger-inner::before {
    top: -6px;
}
.hamburger-inner::after {
    bottom: -6px;
}

/*
 * Collapse
 */
.hamburger--collapse .hamburger-inner {
    top: auto;
    bottom: 0;
    -webkit-transition-delay: 0.15s;
            transition-delay: 0.15s;
    -webkit-transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
            transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    -webkit-transition-duration: 0.15s;
            transition-duration: 0.15s;
}
.hamburger--collapse .hamburger-inner::after {
    top: -12px;
    -webkit-transition: top 0.3s 0.3s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
    opacity 0.1s linear;
    transition: top 0.3s 0.3s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
    opacity 0.1s linear;
}
.hamburger--collapse .hamburger-inner::before {
    -webkit-transition: top 0.12s 0.3s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
    -webkit-transform 0.15s cubic-bezier(0.55, 0.055, 0.675, 0.19);
    transition: top 0.12s 0.3s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
    -webkit-transform 0.15s cubic-bezier(0.55, 0.055, 0.675, 0.19);
    transition: top 0.12s 0.3s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
    transform 0.15s cubic-bezier(0.55, 0.055, 0.675, 0.19);
    transition: top 0.12s 0.3s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
    transform 0.15s cubic-bezier(0.55, 0.055, 0.675, 0.19),
    -webkit-transform 0.15s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.site-head-open .hamburger-inner,
.site-head-open .hamburger-inner::before,
.site-head-open .hamburger-inner::after {
    background-color: var(--color-base);
}

.site-head-open .hamburger-inner {
    -webkit-transition-delay: 0.32s;
            transition-delay: 0.32s;
    -webkit-transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    -webkit-transform: translate3d(0, -6px, 0) rotate(-45deg);
            transform: translate3d(0, -6px, 0) rotate(-45deg);
}

.site-head-open .hamburger-inner::after {
    top: 0;
    opacity: 0;
    -webkit-transition: top 0.3s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
    opacity 0.1s 0.27s linear;
    transition: top 0.3s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
    opacity 0.1s 0.27s linear;
}
.site-head-open .hamburger-inner::before {
    top: 0;
    -webkit-transition: top 0.12s 0.18s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
    -webkit-transform 0.15s 0.42s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: top 0.12s 0.18s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
    -webkit-transform 0.15s 0.42s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: top 0.12s 0.18s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
    transform 0.15s 0.42s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: top 0.12s 0.18s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
    transform 0.15s 0.42s cubic-bezier(0.215, 0.61, 0.355, 1),
    -webkit-transform 0.15s 0.42s cubic-bezier(0.215, 0.61, 0.355, 1);
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

/* Animations
/* ---------------------------------------------------------- */

.transition-fade {
    -webkit-transition: 0.25s;
    transition: 0.25s;
    opacity: 1;
    -webkit-animation: slideUp 0.9s cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
            animation: slideUp 0.9s cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
    -webkit-transform: translate3d(0, 4vh, 0);
            transform: translate3d(0, 4vh, 0);
}
html.is-animating .transition-fade {
    opacity: 0;
}

@-webkit-keyframes slideUp {
  0% {
    -webkit-transform: translate3d(0, 4vh, 0);
            transform: translate3d(0, 4vh, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes slideUp {
  0% {
    -webkit-transform: translate3d(0, 4vh, 0);
            transform: translate3d(0, 4vh, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

