  font-family: 'Lekton', sans-serif;
  letter-spacing: 4px;
  text-decoration: none;
}
.link:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: aqua;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  -webkit-transition-property: transform;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.link:hover, .button:focus, .button:active {
  color: white;
}
.link:hover:before, .button:focus:before, .button:active:before {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}
/*--FOOTER--*/
.footer {
  position: relative;
  padding: 15px;
  margin-top: 50px;
  opacity: .5;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  min-height: 100%;
  margin-bottom: 0;
  display: block;
  align-items: center;
  height: 50px;
  color: white;
  text-align: center;
  font-family: 'Lekton', sans-serif;
  letter-spacing: 3px;
  font-weight: 800;
  text-align: center;
  background-color: black;
  z-index: 99;
}
